// 样式控制
.full-page {
    height: 100vh;
}

.is-center {
    text-align: center;
}

.card-widget {
    padding: 1.5rem 1rem;
    border-radius: .5rem;
}


.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    #header {
        position: relative;
        width: 100%;
        color: #ffffff;

        &::before {
            z-index: -1;
            position: absolute;
            width: 100%;
            height: 100%;
            background-image: url(https://w.wallhaven.cc/full/j5/wallhaven-j5mz95.png);
            filter: brightness(.7);
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            content: '';
        }

        #top-nav {
            display: flex;
            align-items: center;
            width: 100%;
            height: 5rem;
            margin: 0 auto;
            padding: 0 2rem;
            
            span a span {
                flex: 1;
                font-weight: 900;
                font-size: 1.4rem;
            }
        }

        #site-info {
            top: 45%;
            position: absolute;
            width: 100%;
            padding: 0 1rem;

            #title {
                text-shadow: 2px 2px 4px rgba($color: #000000, $alpha: .25);
                font-weight: 900;
                font-size: 3.5rem;
            }

            #sub-title {
                text-shadow: 2px 2px 4px rgba($color: #000000, $alpha: .25);
                font-weight: 450;
                font-size: 1.8rem;
            }
        }
    }    

    #content {
        width: 100%;
        max-width: 100rem;
        display: flex;
        flex: 1 auto;
        margin: 0 auto;
        padding: 40px 15px;
    
        .left {
            width: 26%;
            margin-right: .5rem;

            .author-info {
                background-color: #ffff;

                .avatar {
                    width: 110px;
                    height: 110px;
                    margin: 0 auto;
                    padding: 0 3px 6px 3px;
                    border-radius: 50%;
                    overflow: hidden;
                    
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }

                .author-info__data {
                    margin-top: .5rem;
                }

                .nickname {
                    font-weight: 500;
                    font-size: 2rem;
                }

                .description {
                    font-size: 1rem;
                }
            }
        }

        .right {
            width: 74%;
            margin-left: .5rem;

            .post-item {
                display: flex;
                height: 20rem;
                background-color: #ffff;
                border-radius: .5rem;

                &:not(:first-child) {
                    margin-top: 1rem;
                }
            }
        }
    }
}
